import React,{useEffect,useState} from 'react'
import {Grid,Avatar,Badge,Container} from '@material-ui/core';
import {domain} from '../../../connection/Config'
import Old_orders from './Old_orders'
import { makeStyles } from '@material-ui/core/styles';
import NewOrder from './NewOrder';
import '../../../css/orders.css'



export default function Orders({setLoader,setNewOrder,newOrder,token}) {
  const [preview_orders,setPreview_orders]=useState([])
  useEffect(async() => {
    setLoader(true)
    try{
      const res = await fetch(`${domain}/orders/${token}`)
      const data= await res.json()
      setPreview_orders(data.my_orders) 
      console.log(data.my_orders)
    }
    catch(err){
      console.log(err)
    }

setLoader(false)
  },[])
    return (
      <Container className='page_orders_warper'>
        <Grid className='main_warper'>
          <Grid xl={4} md={4} lg={4}>
            <Grid className="header_warper">
              <h3>הזמנות שבוצעו</h3>
            </Grid>
{preview_orders && preview_orders.length>0?
  preview_orders.map(el=>{
    return(
      <Grid style={{marginTop:'12px'}}>
    <Old_orders
    el={el}
    />
        </Grid>
    )

  })

:<h4>...אין הזמנות קודמות </h4>}
          </Grid>
          <Grid  xl={8} md={8} lg={8}>
            <Grid className="header_warper">
            <h3>הזמנות חדשות</h3>
            </Grid>
            <Grid className='newOrder-warper'>
            {newOrder?
              newOrder.slice(0).reverse().map(el=>{
                return(
                <Grid style={{marginBottom:'15px'}}>
            <NewOrder
            el={el}
            />
               </Grid>
                )
              })
              
            :<h3>אין הזמנות חדשות כרגע</h3>}
            </Grid>
            </Grid>
        </Grid>
      </Container>
    )
}
