import React,{useEffect,useState} from 'react'
import {Grid,Container,TextField,Button} from '@material-ui/core';
import {domain} from '../../connection/Config'
import '../../css/profile.css'

export default function Profile({cooker,setCooker,email,setEmail,address,setAddress,
    phone_number,setPhone_number,setLoader,setStory,story}) {

const update_cooker_info = async()=>{
    setLoader(true)
    try{
        const res2 = await fetch(`${domain}/cookers-admin`,
        {
            method: 'PUT',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(
                {
    
                    address:address,
                    phone_number:phone_number,
                    email:email,
                    id:"lmFHlQjOe6DI5IQknmib" 
                }
                )
        }
        ) 
    }
    catch(err){
        console.log(err)
    }
   
    setLoader(false)
}
   
    return (
        <Container>
            {cooker?
             <Grid  className='container'style={{display:'flex',flexDirection:'column',justifyContent:'center',marginBottom:'20px'}}>
                 <Grid style={{margin:"auto",marginBottom:"20px"}}>
                 <h1 className='profile_header'>{cooker.vendor}</h1>
                 </Grid>
                 <form onSubmit={update_cooker_info}>
               <Grid>
                <Grid>
              <TextField
              type='email' 
              variant='outlined' 
              onChange={(e)=>{setEmail(e.target.value)}} 
              className='text_field' value={email} 
              label='Email:'
              required
              />
                </Grid>
                <Grid>
                <TextField 
                variant='outlined'
                required
                 onChange={(e)=>{setAddress(e.target.value)}} 
                 className='text_field' value={address} 
                 label='Address:'/>
                    </Grid>
                    <Grid>
                    <TextField
                    onChange={(e)=>{setStory(e.target.value)}}
                     variant='outlined'
                      onChange={(e)=>{setPhone_number(e.target.value)}}
                       className='text_field'
                        value={phone_number} 
                        label='Phone_Number:'
                        required/>
                    </Grid>
                    <Grid>
                   <textarea  className='story_field' value={story}/>
                    </Grid>
                    <Grid>
                   <Button type='submit' className='buttons' variant='outlined'>שמור שינויים</Button>
                    </Grid>
                    
            </Grid>
            </form>
            </Grid>
            :<h1 className='profile_header'>פרופיל בשלן לא קיים במערכת...</h1>}
         
        </Container>
    )
}
