import React from 'react'
import { PieChart } from 'react-minimal-pie-chart';
export default function Pie_chart() {
    return (
        <div style={{height:'200px'}}>
           <PieChart
  data={[
    { title: 'August', value: 10, color: '#E38627' },
    { title: 'Total_price', value: 15, color: '#C13C37' },
  ]}
/>; 
        </div>
    )
}

