import React,{useState} from 'react'
import { makeStyles,Grid } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import '../../../css/orders.css'
import { green } from '@material-ui/core/colors';


const useStyles = makeStyles({
    root: {
        minWidth: 275,
      textAlign:'right',
      backgroundColor: "rgb(251, 248, 243)"
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  });

  
export default function NewOrder({el}) {
const [counter,setCounter]=useState(5)
    const readyOrder= async(id)=>{
    alert(id.id)
    }
    const classes = useStyles();
  const bull = <span className={classes.bullet}>•</span>;
    return (
        <Card  className={classes.root}>
      <CardContent>
          <h4> זמן הכנה: דקות <span style={{color:"green"}}>{counter}</span></h4>
      
        
        <Typography variant="h5" component="h5">
        {el.created_at}

        </Typography>
        <Typography  component="h5" color="textSecondary">
         זמן הגעה: {el.arrival_time}
         
        </Typography>
       
        <Typography className={classes.pos} color="textSecondary">
        הערות : {el.comment}
        </Typography>
        <Typography variant="body2" component="p">
          
              {el.items.map(item=>{
                  return(
                      <Grid>
                          <p>{item.amount} {item.name}  </p>
                      </Grid>
                  )
              })
          }
        </Typography>
      </CardContent>
      <div style={{display:'flex'}}>
      <CardActions>
        <Button  variant='outlined' size="small">שנה זמן</Button>
      </CardActions>
      <CardActions>
        <Button onClick={(item)=>{readyOrder(item)}} variant='outlined' size="small">הזמנה מוכנה</Button>
      </CardActions>
      </div>
      
    </Card>
    )
}
