import React,{useState} from 'react'
import clsx from 'clsx';
import { makeStyles,Grid } from '@material-ui/core';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import CloseIcon from '@material-ui/icons/Close';
import MenuIcon from '@material-ui/icons/Menu';
import {Link} from "react-router-dom";
const useStyles = makeStyles({
    list: {
      width: 250,
    },
    fullList: {
      width: 'auto',
    },
  });
  
export default function Responsive_menu({setCurrentPage}) {
  const[closeBt,setCloseBt]=useState(true)
    const classes = useStyles();
  const [state, setState] = React.useState({
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      setCloseBt(true)
      return;
    }
    setCloseBt(false)
    setState({ ...state, [anchor]: open });
  };
    const close_drawer=()=>{}
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List style={{textAlign:'center'}} className='side_nav_warper'>
      <Grid>
           <h3>koki</h3>
            </Grid>
            <Grid className='link_warper'>
            <Link className='nav_link' onClick={()=>setCurrentPage('דוחות פיננסים')}  to='/'>דוחות פיננסים</Link>
            </Grid>
            <Grid className='link_warper'>
            <Link className='nav_link' onClick={()=>setCurrentPage('הזמנות')}  to='/orders'>הזמנות </Link>
            </Grid>
            <Grid className='link_warper'>
            <Link className='nav_link' onClick={()=>setCurrentPage('ביקורות')} to='/reviews'>ביקורות </Link>
                </Grid>
                <Grid className='link_warper'>
                <Link className='nav_link' onClick={()=>setCurrentPage('פרופיל')}  to='/profile'>פרופיל</Link>
                </Grid>
                <Grid className='link_warper'>
                <Link onClick={()=>setCurrentPage('זמינות מנה')} className='nav_link' to='/meals_manage'>זמינות מנה</Link>
                </Grid>
                <Grid className='logo_warper'>
                    <img style={{width:"100%"}} src='Homemade.png'/>
                </Grid>
      </List>
    </div>
  );

  return (
    <div>
      {[ 'right'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button className='menu-bt' onClick={toggleDrawer(anchor, true)}>
            {closeBt?
             <MenuIcon  style={{color:'white'}}/>

            :<CloseIcon  style={{color:'white'}}/>}
           
            </Button>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}
