import React,{useState,useEffect} from 'react'
import Financial_reports from './main-pages/Financial/Financial_reports';
import Reviews from './main-pages/Reviews';
import Profile from './main-pages/Profile';
import Meals_manage from './main-pages/meals_manage/Meals_manage';
import Orders from './main-pages/orders/Orders';
import NavBar from './Headers/NavBar';
import Side_nav from './Headers/Side_nav';
import {Grid} from '@material-ui/core';
import {domain} from '../connection/Config'
import Loading from '../components/Loading_spinner/Loading'
import firebase from 'firebase'
import Responsive_nav from './Headers/Responsive_nav';
import '../css/main.css';
import {
    BrowserRouter as Router,
    Switch,
    Route} from "react-router-dom";
    // import { collection, query, where, onSnapshot } from "firebase";

export default function Main({setToken,token}) {
  
  //listen to screen size changes
    const [width,setWidth]=useState(window.innerWidth)
    React.useEffect(() => {
      function handleResize() {
      setWidth(window.innerWidth) 
      
  }
  
      window.addEventListener('resize', handleResize)
    })
    //temperery connection to fire base, need to make it more secure
    var firebaseConfig = {
        apiKey:"AIzaSyAoW0U5WOew1gvCl18r2qNHidnjJ3xVwRA",
        authDomain:"data-base-food-ordering-app.firebaseapp.com",
        databaseURL:"https://data-base-food-ordering-app-default-rtdb.firebaseio.com",
        projectId:"data-base-food-ordering-app",
        storageBucket:"data-base-food-ordering-app.appspot.com",
        messagingSenderId:"1011228127911",
        appId:"1:1011228127911:web:5b425d5532fd10e1d12182",
        measurementId:"G-D3VVRYBGWH"
      };
      if (!firebase.apps.length) {
        firebase.initializeApp(firebaseConfig);
     }else {
        firebase.app(); // if already initialized, use that one
     }
      const db = firebase.firestore();
    
   
    const [currentPage,setCurrentPage]=useState('דוחות פיננסים')
    const [cooker,setCooker]=useState("")
    const [email,setEmail]=useState('')
    const [address,setAddress]=useState('')
    const [phone_number,setPhone_number]=useState('')
    const [story,setStory]=useState('')
    const[orders_counter,setOrders_counter]=useState(0)
    const[loader,setLoader]=useState(false)
    const[newOrder,setNewOrder]=useState([])
   
            
    
   
    useEffect(async() => {
     
        try{

               //listening to changes in orders collection
            
                     
               db.collection("orders").where("sent_cooker", "==", false)
               .onSnapshot((querySnapshot) => {
               
                   var orders = [];
                   querySnapshot.forEach((doc) => {
                       console.log(doc.data())
                       if(doc.data()){
                        setOrders_counter(orders_counter+1) 
                      doc.data().items=doc.data().items.filter(el=>el.cooker_id==token)
                        orders.push({...doc.data(),id:doc.id});
                        
                         setNewOrder(orders)
                       }
                      
                      
                   });
               });
           
  
            setLoader(true)
            
                //getting data of the cooker
                const res= await fetch(`${domain}/cookers-admin/${token}`)
                const data = await res.json()
                setCooker(data.cooker)
                setEmail(data.cooker.Email)
               setAddress(data.cooker.address)
               setPhone_number(data.cooker.phone_number)
              setStory(data.story)  
        
    }
        catch(err){
            console.log(err)
        }
        setLoader(false)
    }, [])
    return (
        
<Router>
      <Grid>
       <Loading
       loader={loader}
       />
          
  <Grid className='headers-warper'>
    
<Grid xl={10} md={12} lg={10} className='header-nav'>
      {width>950?
<NavBar
currentPage={currentPage}
orders_counter={orders_counter}
setOrders_counter={setOrders_counter}
setCurrentPage={setCurrentPage}
/>
 :<Responsive_nav
 currentPage={currentPage}
orders_counter={orders_counter}
setOrders_counter={setOrders_counter}
setCurrentPage={setCurrentPage}
width={width}
 />}
<Grid className='page_warper'>

    
<Switch>

<Route path='/reviews'>
      <Reviews
      setLoader={setLoader}
      />
     </Route>

<Route path='/meals_manage'>
      <Meals_manage
      setLoader={setLoader}
      setToken={setToken}
      token={token}
      />
     </Route>

<Route path='/profile'>
      <Profile
      cooker={cooker}
      setCooker={setCooker}
      email={email}
      setEmail={setEmail}
      address={address}
      setAddress={setAddress}
      phone_number={phone_number}
      setPhone_number={setPhone_number}
      setStory={setStory}
      story={story}
      setToken={setToken}
      token={token}
      />
     </Route>

     <Route path='/orders'>
      <Orders
       setToken={setToken}
       token={token}
      setLoader={setLoader}
      setNewOrder={setNewOrder}
      newOrder={newOrder}
      />
     </Route>
     
     <Route path='/'>
      <Financial_reports
       setToken={setToken}
       token={token}
      setLoader={setLoader}
      />
     </Route>
</Switch>
</Grid>
</Grid>
{width>950?
<Grid xl={2} md={0} lg={2} className='side_bar'>
    <Side_nav
    setCurrentPage={setCurrentPage}
    />
</Grid> 
:<div style={{display:"none"}}></div>} 
 </Grid>
      </Grid>  
      </Router>
        
        
    )
}
