import React from 'react'
import {Grid} from '@material-ui/core';
import {Link} from "react-router-dom";
export default function Side_nav({setCurrentPage}) {
    return (
        <div className='side_nav_warper'>
            <Grid>
           <h3>koki</h3>
            </Grid>
            <Grid className='link_warper'>
            <Link  className='nav_link' onClick={()=>setCurrentPage('דוחות פיננסים')}  to='/'>דוחות פיננסים</Link>
            </Grid>
            <Grid className='link_warper'>
            <Link className='nav_link' onClick={()=>setCurrentPage('הזמנות')}  to='/orders'>הזמנות </Link>
            </Grid>
            <Grid className='link_warper'>
            <Link className='nav_link' onClick={()=>setCurrentPage('ביקורות')} to='/reviews'>ביקורות </Link>
                </Grid>
                <Grid className='link_warper'>
                <Link className='nav_link' onClick={()=>setCurrentPage('פרופיל')}  to='/profile'>פרופיל</Link>
                </Grid>
                <Grid className='link_warper'>
                <Link onClick={()=>setCurrentPage('זמינות מנה')} className='nav_link' to='/meals_manage'>זמינות מנה</Link>
                </Grid>
                <Grid className='logo_warper'>
                    <img style={{width:"100%"}} src='Homemade.png'/>
                </Grid>
        </div>
    )
}
